import React, { memo } from 'react';
import classnames from 'classnames';
import { ButtonColor } from '#types/Styles';
import * as S from './style';

export interface Props {
  buttonColor?: ButtonColor;
  children: React.ReactNode | string;
  className?: string[];
  disabled?: boolean;
  href?: string;
  /* eslint-disable  @typescript-eslint/no-explicit-any */
  onClick?: (e?: any) => void;
  to?: string;
  type?: 'button' | 'reset' | 'submit';
}

/**
 * @property buttonColor - background, hover, disabled, text 
 * @property children(required) -   ,  +
 * @property className -  class name
 * @property disabled -    disabled
 * @property href - anchor    , href props url 
 * @property onClick -      
 * @property to -    , to page route 
 * @property type -  type(button, reset, submit)
 */
const Button = ({
  buttonColor,
  children,
  className = [],
  disabled = false,
  href,
  onClick,
  to,
  type = 'button',
  ...props
}: Props) => {
  //   link  
  if (to) {
    return (
      <S.LinkButton
        to={to}
        className={classnames(...className)}
        buttonColor={buttonColor}
        {...props}
      >
        {children}
      </S.LinkButton>
    );
  }

  // anchor  
  if (href) {
    return (
      <S.AnchorButton
        href={href}
        className={classnames(...className)}
        buttonColor={buttonColor}
        {...props}
      >
        {children}
      </S.AnchorButton>
    );
  }

  //   
  return (
    <S.Button
      type={type}
      className={classnames(...className)}
      onClick={onClick}
      disabled={disabled}
      buttonColor={buttonColor}
      {...props}
    >
      {children}
    </S.Button>
  );
};

export default memo(Button);
