import React, { FunctionComponent, RefObject, memo } from 'react';
import classnames from 'classnames';
import { ColorType } from '#types/Styles';
import * as S from './style';

export interface Props {
  backgroundColor?: ColorType;
  className?: string[];
  icon: FunctionComponent;
  innerRef?: RefObject<HTMLSpanElement>;
  isCircled?: boolean;
  /* eslint-disable  @typescript-eslint/no-explicit-any */
  onClick?: (e?: any) => void;
}

/**
 * @property backgroundColor -   color
 * @property className -  class name
 * @property icon(required) -  
 * @property innerRef -   Icon ref    
 * @property isCircled - border radius 50%   (true  )
 * @property onClick -      
 */
const IconWrapper = ({
  backgroundColor,
  className = [],
  icon,
  innerRef,
  isCircled,
  onClick,
}: Props) => {
  const IconComponent = icon;

  //     
  if (onClick) {
    return (
      <>
        <S.IconWrapper
          backgroundColor={backgroundColor}
          className={classnames(...className, { circle: isCircled }, 'icon-button')}
          onClick={onClick}
          ref={innerRef}
          role="button"
        >
          <IconComponent />
        </S.IconWrapper>
      </>
    );
  }

  //    
  return (
    <S.IconWrapper className={classnames(...className)} ref={innerRef}>
      <IconComponent />
    </S.IconWrapper>
  );
};

export default memo(IconWrapper);
