import React from 'react';
import * as S from './style';
import { Dropdown, IconPaths, IconWrapper } from '#components/Atoms';

export interface Props {
  onClickDropdownItem: (sortBy: string) => void;
}

export default function Categories({ onClickDropdownItem }: Props) {
  const categories = [
    { text: '', value: 'total', icon: IconPaths.Glitter },
    { text: '', value: 'marketing', icon: IconPaths.Writing },
    { text: '', value: 'design', icon: IconPaths.Palette },
    { text: '', value: 'plan', icon: IconPaths.Bulb },
    { text: '', value: 'develop', icon: IconPaths.Laptop },
  ];

  return (
    <S.Categories>
      <Dropdown onClickDropdownItem={onClickDropdownItem} />
      {categories.map(({ text, value, icon }) => (
        <S.Category key={text}>
          <S.Input type="radio" id={value} name="category-radio-group" />
          <S.Label htmlFor={value} backgroundColor={S.backgroundColors[text]}>
            {text}
            <IconWrapper icon={icon} />
          </S.Label>
        </S.Category>
      ))}
    </S.Categories>
  );
}
