import { createSlice } from '@reduxjs/toolkit';

const { actions, reducer: conditionReducer } = createSlice({
  name: 'condition',
  initialState: {
    sortBy: '',
  },
  reducers: {
    setSortBy(state, { payload: sortBy }) {
      return {
        ...state,
        sortBy,
      };
    },
  },
});

export const { setSortBy } = actions;
export default conditionReducer;
